<template>
<div class="container no-padd padd-top">
    <div>
        <!-- <el-carousel :autoplay="true" :loop="true" :interval="3000" direction="horizontal">
            <el-carousel-item class="border-radius" v-for="(data1, index) in data" :key="index">
                <div class="item style-slider">
                    <img img class="img-lazy" :src="'https://go2joylocal.s3-ap-southeast-1.amazonaws.com/'+data1.imagePath" alt="image" />
                </div>
            </el-carousel-item>
        </el-carousel> -->
        <!-- <div v-swiper:mySwiper="swiperOption" class="swiper">
            <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="(data1, index) in data" :key="index">
                    <img img class="img-lazy res-ponsive border-radius" :src="'https://go2joylocal.s3-ap-southeast-1.amazonaws.com/'+data1.imagePath" alt="image" />
                </div>
            </div>
            <div class="swiper-pagination swiper-pagination-bullets" slot="pagination">123</div>
            <div class="swiper-button-prev" slot="button-prev"></div>
            <div class="swiper-button-next" slot="button-next"></div>
        </div> -->
        <b-carousel id="carousel-1" v-model="slide" :interval="4000" controls indicators background="#ababab" img-width="1024" img-height="480" style="text-shadow: 1px 1px 2px #333;" @sliding-start="onSlideStart" @sliding-end="onSlideEnd">
            <!-- Text slides with image -->
            <b-carousel-slide v-for="(data1, index) in data" :key="index" class="img-lazy res-ponsive border-radius" :img-src="'https://go2joylocal.s3-ap-southeast-1.amazonaws.com/'+data1.imagePath" >
            
            </b-carousel-slide>

        </b-carousel>
    </div>
</div>
</template>

<script>
import VueAwesomeSwiper from 'vue-awesome-swiper'

import {
    Swiper,
    SwiperSlide
} from 'vue-awesome-swiper'
import axios from 'axios';
export default {
    name: 'Slider',
    components: {
        Swiper,
        SwiperSlide
    },
    data() {
        return {
            data: {

            },
            slide: 0,
            sliding: null
        }
    },

    created() {
        this.getList();
    },
    methods: {
        async getList() {
            axios.defaults.headers = {
                'deviceid': 'device_for_web',
            }

            let {
                data
            } = await axios.get('http://118.69.235.218:8080/hotelapi/home/view/findHomePageInfo');

            let listBanner = data.bannerFormList;
            //console.log('this.data : ', this.data);

            this.data = listBanner
            console.log('this.data.bannerslide', this.data);
        },
        onSlideStart(slide) {
            this.sliding = true
        },
        onSlideEnd(slide) {
            this.sliding = false
        }
    },

}
</script>

<style scoped>
#carousel-1{
    border-radius: 30px !important;
}
#carousel-1 .carousel-item{
    border-radius: 30px !important;
}
.img-fluid{
    border-radius: 30px !important;
}

.style-slider {
    display: block;
    overflow: hidden;
    border-radius: 20px;
    margin: 0 auto 0px auto;
}

.border-radius {
    border-radius: 40px;
}

.res-ponsive {
    width: 100%;

}

.style-slider img {
    width: 100%;

}

.padd-l-r-10 {
    padding: 0 10px 0 10px;
}

.el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
}

.padd-top {
    padding-top: 30px
}

@import 'swiper/css/swiper.css';
@import './base.scss';

.swiper .swiper-pagination-bullet-custom {
    font-size: 20px;
    width: 100px !important;
    height: 100px !important;
    line-height: 100px !important;
    text-align: center;
    color: red;
    opacity: 0.7;
    background: rgba(red, 0.2);
    /* transition: all $transition-time-normal; */
}

.swiper .swiper-pagination-bullet-custom:hover {
    opacity: 1;
}

.swiper .swiper-pagination-bullet-custom .swiper-pagination-bullet-active {
    opacity: 1;
    color: #ffffff;
    background: #007aff;

}
</style>



