<template>
<div class="container">
    <p class="style-title">Chính sách</p>
    <p>Giờ bắt đầu theo giờ : từ 7h.</p>
    <p>Giờ nhận phòng theo ngày : từ 14h.</p>
    <p>Giờ trả phòng theo ngày : từ 12h.</p>
    <p>Qua đêm từ 22h ~ 12h</p>
    <p>Hủy đặt phòng thoải mái 1.0 giờ trước khi nhận phòng</p>
    <p class="style-title"> Giới thiệu</p>
    <!-- <p class="title-content"> {{ data.description }}</p> -->
    <div class="col-12">
        <div class="col-4">&nbsp; </div>
        <div class="col-4">
            <button class="btn btn-primary style-btn-dat">Đặt ngay</button>
        </div>
        <div class="col-4"> &nbsp;</div>
    </div>
</div>
</template>

<script>
import axios from "axios"

export default {
    name: "AboutHotel",
    data() {
        return {

        };
    },
    created() {
        this.getList();
    },

    methods: {
        async getList() {
            axios.defaults.headers = {
                'deviceid': 'device_for_web',
            }

            let {
                data
            } = await axios.get('http://118.69.235.218:8080/hotelapi/hotel/view/viewHotelDetailNew?hotelSn=190');
            console.log('data', data.data)
            this.data = data.data
            console.log('this.data', this.data)
        },
    },

};
</script>

<style scoped>

.style-btn-dat{
    width: 100%;
}
</style>



