<template>
<div>
    <Header />
    <div class="container">
        <div class="col-12">
            <img class="img-responsive" src="./../../assets/404.jpg" alt="">
        </div>
        <div class="col-12 style-padding-bottom">
            <p class="font-title-error style-can-giua"> <strong class="style-opps"> Oops ! </strong> <span>.... The pages you requested could not be found</span> </p>
            <p class="back-home style-can-giua"><span><router-link tag="a" :to="{ name: 'Home'}"> Quay trở lại trong chủ</router-link></span></p>
        </div>
       
    </div>
    <Footer />
</div>
</template>
<script>
import Vue from 'vue'
// import HelloWorld from "./components/HelloWorld.vue";
import Header from "./../Home/Header.vue"
import Footer from "./../Home/Footer.vue"
// import VueI18n from 'vue-i18n'
// Vue.use(VueI18n)
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
export default {
    path: '/error404',
    name: "Error404",
    components: {
        Header,Footer
    },
    data() {
        return {
            userSelectedFromChild: {}
        }
    },
    methods: {

        childrenSelectUser(dataHotel) {
            console.log(dataHotel)
            this.userSelectedFromChild = dataHotel
        }
    }

};
</script>

<style scoped>
.img-responsive{
    width: 100%;
    height: auto;
}
.font-title-error{
    font-size: 26px;
}
.style-opps{
    font-size: 54px;
}
.back-home{
    font-size: 24px;
    color:#ff6400;
}
.style-can-giua{
        display: flex;
    justify-content: center;
}
.style-padding-bottom{
    padding-bottom: 50px;
}
</style>



