<template>
<div>
    <Header />

    <div class="container">
        <div class="whatevershare">
            <p class="style-share"><button class="btn btn-light style-i-share"><span>Share</span> <i class="fas fa-share-alt "></i></button></p>
        </div>
        <carousel :navigationEnabled="false" :perPageCustom="[[480, 1], [768, 1], [1024, 1]]">
            <slide v-for="(data0, index) in data" :key="index">
                <div class="col-12">
                    <div class="item style-slider">
                        <!-- <img :src="'https://go2joylocal.s3-ap-southeast-1.amazonaws.com/'+data0" alt="image" /> </div> -->
                        <img :src="'https://go2joylocal.s3-ap-southeast-1.amazonaws.com/'+ data0.imagePath" alt="">

                    </div>
                </div>

            </slide>
        </carousel>
        <div class="whatever">
            <p class="style-count-image">1/23</p>
        </div>
    </div>
</div>
</template>

<script>
import axios from 'axios';
import Header from './../Home/Header'
export default {
    name: 'SliderDetail',
    components: {
        Header,

    },
    data() {
        return {
            data: {

            },
        }
    },

    created() {
        this.getList();
    },
    methods: {
        async getList() {
            axios.defaults.headers = {
                'deviceid': 'device_for_web',
            }
            let {
                data
            } = await axios.get('http://118.69.235.218:8080/hotelapi/hotel/view/viewHotelDetailNew?hotelSn=190');
            let listBanner = data.data.justHotelImageList;
            this.data = listBanner;
        },
    },
}
</script>

<style scoped>
.whatever {
    background-image: -webkit-gradient(linear,
            left top,
            left bottom,
            color-stop(1, #ffffff),
            color-stop(0, rgba(0, 0, 0, 0)));
    background-image: -o-linear-gradient(bottom, #ffffff 0%, rgba(0, 0, 0, 0) 100%);
    background-image: -moz-linear-gradient(bottom, #ffffff 0%, rgba(0, 0, 0, 0) 100%);
    background-image: -webkit-linear-gradient(bottom, #ffffff 0%, rgba(0, 0, 0, 0) 100%);
    background-image: -ms-linear-gradient(bottom, #ffffff 0%, rgba(0, 0, 0, 0) 100%);
    background-image: linear-gradient(to top, #ffffff 0%, rgba(0, 0, 0, 0) 100%);
    height: 45px;
    position: relative;
    width: 100%;
    top: -45px;
}

.style-share {
    width: 100%;
    text-align: right;
    padding-right: 150px;

}

.style-share button {
    opacity: 0.9;
    border-radius: 20px;
}

.style-share i,
span {
    color: #ff6401;
    font-size: 15px;
    padding: 3px;
    ;
}

.style-i-share {
    font-size: 18px;
    position: absolute;
    z-index: 1;
    margin-top: 20px;
}

.style-count-image {
    width: 5%;
    background: #333333;
    float: right;
    color: #ffffff;
    opacity: 0.9;
    margin-right: 30px;
    font-size: 18px;
    border-radius: 10px;
    text-align: center;
}

.style-box-shadow {
    background: #ffffff;
    border-radius: 20px;
    box-shadow: 0 3px 10px rgba(0, 0, 0, .1);
}

.style-slider {
    max-height: 748px;
    border-radius: 0px;
}

@media only screen and (max-width: 350px) and (min-width: 300px) {
    .style-slider {
        max-height: 230px;
    }

    .style-count-image {
        width: 50px;
        margin: 5px;
        border-radius: 0px;
        font-size: 12px;
    }

    .whatever {
        height: 50px;
        top: -50px;
    }
}

@media only screen and (max-width: 400px) and (min-width: 351px) {
    .style-slider {
        max-height: 270px;
    }

    .style-count-image {
        width: 50px;
        margin: 5px;
        border-radius: 0px;
        font-size: 12px;
    }

    .whatever {
        height: 50px;
        top: -50px;
    }
}

@media only screen and (max-width: 480px) and (min-width: 401px) {
    .style-slider {
        max-height: 310px;
    }

    .style-count-image {
        width: 50px;
        margin: 5px;
        border-radius: 0px;
        font-size: 12px;
    }

    .whatever {
        height: 50px;
        top: -50px;
    }
}

@media only screen and (max-width: 2560) and (min-width: 1920px) {
    .style-slider {
        max-height: 310px;
    }

    .style-count-image {
        width: 50px;
        margin: 5px;
        border-radius: 0px;
        font-size: 12px;
    }

    .whatever {
        height: 50px;
        top: -50px;
    }
}
</style>



