<template>
<div class="container">
    <b-row>
        <div class="col-12 float-left padding-0-5-10">
            <div class="col-6 style-color-xanh font-title-home">{{ $t('Home.special') }}</div>
            <div class="col-6 style-text-align-right style-area">
                <span class="color-000" style="cursor: pointer" @click="chooseSpecitalHotel(dataHotel)">{{ $t('Home.seeall') }}</span></div>
        </div>
        <!-- <input
            placeholder="Enter Text Here"
            v-model="tempMessage"
        />
        <div>
            <button @click="sendMessage">Emit data</button>
        </div> -->
        <div class="col-12">
            <carousel :centerMode="false" :loop="true" :perPage="1" :navigationEnabled="false" paginationColor="#7e7e7e" paginationPosition="bottom" :perPageCustom="[[480, 1], [768, 2], [1024, 2]]">
                <slide v-for="(dataHotel, index) in data" :key="index">
                    <div class="col-11 style-box-shadow padd-left-right-0  no-padd margin-20">
                        <div class="col-4  no-padd padd-left-right-0" style="float:left">
                            <div class="hotel-item">
                                <!-- <img class="img-lazy" :src="'https://go2joylocal.s3-ap-southeast-1.amazonaws.com/'+data1.imagePath"> -->
                                <!-- <img src="https://go2joylocal.s3-ap-southeast-1.amazonaws.com/hotel/806_1525842113614/2_806_66_1525842113795.jpg" alt=""> -->
                                <router-link tag="a" :to="{ name: 'DetailHotel', params: { Sn: 1 }}">
                                    <el-image class="img-lazy scale-hover" :src="'https://go2joylocal.s3-ap-southeast-1.amazonaws.com/'+dataHotel.imagePath" lazy placeholder="Images loading...">
                                        <div slot="error">
                                            <img class="img-lazy scale-hover" src="./../../assets/loading_big.png" alt="">
                                        </div>
                                    </el-image>
                                </router-link>
                            </div>
                        </div>
                        <div class="col-8 style-padd">
                            <p class="style-bold font-size-title-name color-den style-gioi-han-dong">{{ dataHotel.name }}</p>
                            <p class="padding-p"> <a class="Rectangle-1158 Shape style-color-ff6500 style-padding-text"><i class="fas fa-star"></i> <span>{{ dataHotel.averageMark }} ({{ dataHotel.totalFavorite }}) </span></a>
                            </p>
                            <!-- <p class="padding-p"><a class="style-back-xanh font-size-title"><i class="fas fa-clock"></i> <span> 0/5 </span></a></p> -->
                            <p class="padding-p"><a class="Rectangle-1158 style-padding-text font-size-title-district"><i class="fas fa-map-marked"></i> <span> {{ dataHotel.districtName }} </span></a></p>
                            <div class="float-right style-border-cam Group-6-specital">
                                <p class="style-margin-1 font-size-title"><a class="style-back-cam"><i class="fas fa-bolt"></i> <span class="FlashSale"> Giảm giá đặc biệt </span></a><a></a><span class="style-number-room-specital"> Còn lại 1 phòng</span></p>
                            </div>
                            <br>
                            <div class="float-right style-padd-10">
                                <p class="color-den"><span class="style-qua-dem first-hours-VND"> Qua đêm </span><span class="first-hours-VND"> VND</span><span class=" font-size-title-price"> &nbsp;{{ dataHotel.lowestOneDay }}</span></p>
                            </div>
                        </div>

                    </div>
                </slide>

            </carousel>
        </div>
    </b-row>
</div>
</template>

<script>
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import Vue from 'vue';
import VueCarousel from 'vue-carousel';
import axios from 'axios';
Vue.use(VueCarousel);
export default {
    name: 'Specital',
    data() {
        return {
            data: {
                sn: 0
            },
            tempMessage: ''
        }
    },

    async created() {
        axios.defaults.headers = {
            'deviceid': 'device_for_web',
        }
        let {
            data
        } = await axios.get('http://118.69.235.218:8080/hotelapi/home/view/findHomePageInfo');
        let data1 = data.detailCollectionList;
        let HotHotel = (data1[1]);
        let dsHotHotel = HotHotel.hotelFormList
        this.data = dsHotHotel;
        this.sn = dsHotHotel.sn

    },
    methods: {
        chooseSpecitalHotel(dataHotel, sn) {
            this.$emit('dsHotel', dataHotel)
            // console.log('1234')
            this.$store.dispatch({
                type: 'updateDataRoom',
                data: this.data,
                sn: this.sn
            });
            this.$router.push({
                name: 'Category',
                params: {
                    Sn: 2
                }
            })
        },
        sendMessage() {
            this.$store.commit("SET_MESSAGE", this.tempMessage);
            this.tempMessage = '';
        }
    },
    // computed: {
    //     roomData() {
    //         return this.$store.state.dataRoom
    //     }
    // },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>

</style>



